import React, { Component as Cp } from "react";
import styles from "./SettingSection.module.css";
import Switch from "../SettingItems/Switch";
import WeekRange from "../SettingItems/WeekRange";
import { SettingEntries, SettingEntry, SettingTypes } from "./SettingEntries";
import NumberInput from "../SettingItems/NumberInput";
import { Data } from "../../types/data";

export type SettingData = Data["config"];
/**@once*/
export default class SettingSection extends Cp<SettingData>{
    render() :React.ReactNode{
        return(
            <div className={styles.wrapper}>
                <h5></h5>
                <WeekRange
                    title=""
                    description="<>-<>"
                />
                <NumberInput
                    title=""
                    description=""
                />
                <h5><small></small></h5>
                <NumberInput
                    title=""
                    description=""
                />
                <NumberInput
                    title=""
                    description=""
                />
                <NumberInput
                    title=""
                    description=""
                />
                <h5></h5>
                <Switch
                    id="show-weekend"
                    title=""
                    description=""
                    checked={this.props.showWeekend}
                />
                <Switch
                    id="start-at-sunday"
                    title=""
                    description=""
                    checked={this.props.startWeekAtSunday}
                />
                <Switch
                    id="show-other-weeks"
                    title=""
                    description=""
                    checked={false}
                />
                <NumberInput
                    title=""
                    description=" 500 "
                />
                <h5></h5>
                <Switch
                    id="lock-data"
                    title=""
                    description=""
                    checked={false}
                />
                <Switch
                    id="offline-mode"
                    title=""
                    description=""
                    checked={false}
                />
                <Switch
                    id="notification"
                    title=""
                    description=" PC  Chrome "
                    checked={false}
                />
            </div>
        );
    }
}